import React from 'react';
import {NavLink} from "react-router-dom";

export class Navbar extends React.Component{
    render(){
        return(
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <NavLink className="navbar-brand" to="/">$ Code_Era</NavLink>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
          
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item active">
                  <NavLink className="nav-link" to="/">Home <span class="sr-only">(current)</span></NavLink>
                </li>
                <li className="nav-item active">
                  <NavLink className="nav-link" to="/About">About</NavLink>
                </li>
                <li class="nav-item dropdown active">
                    <NavLink class="nav-link dropdown-toggle" to="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      Fields
                    </NavLink>
                    <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                      <NavLink class="dropdown-item" to="/Code">Competetive Programming</NavLink>
                      <NavLink class="dropdown-item" to="/Web">Web Devlopment</NavLink>
                      <NavLink class="dropdown-item" to="/Android">Android Devlopment</NavLink>
                    </div>
                  </li>
                <li className="nav-item active">
                  <NavLink className="nav-link" to="/Contact">Contact us</NavLink>

                </li>
              </ul>
              <ul className="navbar-nav ml-auto ">
              <li className="nav-item active">
                  <NavLink  to="/Login" className="btn btn-success">Log in</NavLink>

                </li>
                <li className="nav-item active mx-2 mr-4">
                  <NavLink  to="/Signup" className="btn btn-success">Sign-up</NavLink>

                </li>
              </ul>
            </div>
          </nav>
        );
    }
}