import React from 'react';
import {Label,Input} from 'reactstrap';
import {NavLink} from "react-router-dom";
export class Contact extends React.Component{
    render(){
        return(
            <div className="container">
            <h2>To contact us follow:</h2>
            <form>
                <div className="form-group">
                  <Label for="exampleFormControlInput1">Email address</Label>
                  <Input type="email" className="form-control" id="exampleFormControlInput1" placeholder="name@example.com"/>
                </div>
                <div className="form-group">
                  <Label for="exampleFormControlSelect1">Example select</Label>
                  <select className="form-control" id="exampleFormControlSelect1">
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                  </select>
                </div>
                <div className="form-group">
                  <Label for="exampleFormControlSelect2">Example multiple select</Label>
                  <select multiple className="form-control" id="exampleFormControlSelect2">
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                  </select>
                </div>
                <div className="form-group">
                  <Label for="exampleFormControlTextarea1">Write your queries</Label>
                  <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"on></textarea>
                </div>
                <NavLink type="submit" className="btn btn-danger" to="/">Submit</NavLink>
              </form>
        </div>
        );
    }
}