import React from 'react';
import {NavLink} from "react-router-dom";
export class Carousel extends React.Component{
    render(){
        return(
            <div id="carouselExampleCaptions" className="carousel slide" data-ride="carousel">
            <ol className="carousel-indicators">
                <li data-target="#carouselExampleCaptions" data-slide-to="0" className="active"></li>
                <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
                <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
            </ol>
            <div className="carousel-inner">
                <div className="carousel-item active">
                    <img src="138.jpg" className="img-fluid" alt="..." />
                    <div className="carousel-caption d-none d-md-block">
                        <h2 >Welcome to My Group</h2>
                        <p>This place provides you sources and things.</p>
                        <NavLink  to="/Code" className="btn btn-primary mx-2">Code</NavLink>
                  <NavLink  to="/Web" className="btn btn-secondary mx-2">Web Design</NavLink>
                  <NavLink to="/Android" className="btn btn-success mx-2">App Launch</NavLink>
                    </div>
                </div>
                <div className="carousel-item">
                    <img src="136.jpg" className="img-fluid" alt="..." />
                    <div className="carousel-caption d-none d-md-block">
                        <h5>Web Designing</h5>
                        <p>To explore your ideas on web.</p>
                    </div>
                </div>
                <div className="carousel-item">
                    <img src="137.jpg" className="img-fluid" alt="..." />
                    <div className="carousel-caption d-none d-md-block">
                        <h5>Android</h5>
                        <p>Launch your apps and need to help ask for your querie.</p>
                    </div>
                </div>s
            </div>
            <a className="carousel-control-prev" href="#carouselExampleCaptions" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleCaptions" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
        </div>
        );
    }
}